<template>
    <div>
        {{count}}
        <button @click="add()">add</button>
    </div>
</template>

<script>
    export default {
        data(){
            return {}
        },
        computed: {
            count() {
                return this.$store.state.count
            }
        },
        methods: {
            add() {
                this.$store.commit('add')
            }
        },
    }
</script>

